.spinner {
  text-align: center;
  margin: 10rem auto;
}

.spinner img {
  width: 10rem;
  height: 10rem;
  animation: 2s linear infinite rotate;
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.error {
  background-color: #f62f2f;
  align-items: center;
  width: 50rem;
  margin: 2rem;
  display: flex;
}

.error img {
  width: auto;
  height: 14rem;
  margin: 1rem;
}

.error p {
  color: #fff;
  font-family: Courier New, Courier, monospace;
  font-size: 1.8rem;
  line-height: 1.8;
}
