.spinner {
  margin: 10rem auto;
  text-align: center;

  img {
    height: 10rem;
    width: 10rem;
    // height: 6rem;
    // width: 6rem;
    // fill: #f38e82;
    animation: rotate 2s infinite linear;
  }
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

