.error {
  display: flex;
  align-items: center;
  margin: 2rem;
  background-color: #f62f2f;
  width: 50rem;

  img {
    height: 14rem;
    width: auto;
    margin: 1rem;
  }

  p {
    font-size: 1.8rem;
    color: #fff;
    line-height: 1.8;
    font-family: "Courier New", Courier, monospace;
  }
}

